//header
header {
    position: fixed;    
    z-index: 100;
    width: 100%;  
    background-color: #FFF;
    height: 117px;
}
.header-container {
    display: flex;
    margin: 0 auto;
    padding: 50px 0 35px;
    max-width: 1180px;
}
.header-logo {
    margin: 0 30% 0 60px;
    font-size: 32px;
    font-weight: 700;
    color: $color2;
    text-decoration: none;
    transition-duration: 0.3s;
    &:hover {
        opacity: 0.8;
    }
    &:active {
        color: #3F4A5C;
    }
}
.header-menu {
    margin: 12px 60px 0 0;
    display: flex;
    justify-content: space-between;
    &__item {
        list-style-type: none;
        margin-right: 45px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        font-size: 16px;
        font-weight: 400;
        letter-spacing: 0.01em;
        color: $color1;
        text-decoration: none;
        text-transform: uppercase;
        cursor: pointer;
    }
}
.header-submenu-conteiner {
    position: relative;
    padding-right: 20px;
    transition-duration: 0.3s;
    &__item {
        position: absolute;
        width: 12px;
        height: 8px;
        top: 7px;
        left: calc(100% + -9px);
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 2px;
            width: 10px;
            background-color: $color1;
            transform: rotate(45deg);
        }
        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 6px;
            height: 2px;
            width: 10px;
            background-color: $color1;
            transform: rotate(-45deg);
        }
    }
    &:hover .header-submenu {
        height: 150px;
    }
}
.cart {
    position: relative;
    margin-top: 4px;
    text-decoration: none;
    &__icon {
        font-size: 26px;
    }
    &__count {
        position: absolute;
        top: 8px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 16px;
        font-weight: 700;
        color: #fff;
    }
}

.header-submenu {
    display: block;
    position: absolute;
    background-color: #fff;
    text-align: left;
    padding-top: 20px;
    width: calc( 100% + 50px);
    margin-left: -20px;
    overflow: hidden;
    height: 0;
    transition: height 0.6s, padding 0.6s;
    &__link {
        padding: 5px 20px 5px;
        display: block;
        letter-spacing: 0.01em;
        color: rgb(157,190,223);
        text-decoration: none;
        &:hover {
            background-color: rgb(157,190,223);
            color: #fff;
        }
    }
}
