//sale
.s-sec1 {
    padding-top: 165px;
    &__h3 {
        margin-bottom: 30px;
    }
}
.s-sec1-box {
    height: 260px;
    position: relative;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
    cursor: pointer;
    &__decore {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: 0;
        transition: opacity 0.8s;
    }
    &__price {
        position: absolute;
        left: 0;
        bottom: 0;
        padding: 20px 0;
        width: 100%;
        background-color: #9dbedf;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 0.01em;
        text-align: center;
        color: #fff;
        opacity: 1;
        transition: opacity 0.8s;
    }
    &__btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 23px 0;
        width: 180px;
        text-align: center;
        opacity: 0;
        transition: opacity 0.8s;
    }
    &:hover {
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    }
    &:hover .s-sec1-box__btn{
        opacity: 1;     
    }
    &:hover .s-sec1-box__price{
        opacity: 0;     
    }
    &:hover .s-sec1-box__decore{
        opacity: 0.6;     
    }
}
.s-sec2 {
    position: relative;
    margin: 70px 0 120px;
    text-align: center;
    &__h3 {
        margin-bottom: 30px;
