        text-decoration: none;
        text-transform: uppercase;
        border-bottom: 2px dashed currentColor;
    }
}
.product-box {
    &__container {
        margin-bottom: 30px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    position: relative;
    margin-bottom: 20px;
    width: 23%;
    height: 200px;
    font-size: 16px;
    color: $color2;
    letter-spacing: 0.01em;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    text-decoration: none;
    &--toggle-grid {
        height: 320px;
        width: 48.7179%;
        font-size: 20px;
        .product-box__img {
            margin: 0;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            max-width: 100%;
        }
        .product-box__title {
            position: absolute;
            z-index: 10;
            margin: 0;
            width: 100%;
            left: 7.894%;
            bottom: 90px;
            margin: 0 0 10px;
            font-size: 20px;
            text-align: left;
        }
        .product-box__subtitle {
            position: absolute;
            z-index: 10;
            margin: 0;
            width: 100%;
            left: 7.894%;
            bottom: 60px;
            text-align: left;
        }
        .product-box__price {
            bottom: 20px;
            left: 7.894%;
            justify-content: flex-start;
        }
    }
    &:hover {
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    }
    &__img {
        margin-top: 25px;
        max-width: calc(100% - 30px);
        height: auto;
    }
    &__title {
        margin: 20px auto 5px;
        text-transform: uppercase;
    }
    &__price {
        display: flex;
        justify-content: center;
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 10px;
    }
    &__old-price {
        margin-right: 11.111111%;
        text-decoration: line-through;
    }
    &__new-price {
        color: $color1;
    }
}
//category
.cat-sort {
    margin: 125px 0 40px;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.02em;
    color: $color2;
    &__select {
        font-weight: 400;
        color: $color1;
        border: none;
        outline: none;
    }
}
.cat-show {
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0.02em;
    color: #9DBEDF;
    text-align: left;
}
.s-sec2__h2 {
    margin-bottom: 15px;
    color: $color2;
    text-align: left;
}
.cat-filter {
    margin-top: 125px;
    &__title {
        margin-bottom: 30px;
        font-size: 16px;
        letter-spacing: 0.01em;
        color: #9DBEDF;
    }
    &__box {
        margin-bottom: 30px;
    }
    &__name {
        display: inline-block;
        position: relative;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: 0.02em;
        color: $color1;
    }
    &__decore {
        position: absolute;
        top: 5px;
        right: -20px;
        height: 8px;
        width: 13px;
        cursor: pointer;
        &:before {
            content: "";
            position: absolute;
            top: 3px;
            left: -2px;
            height: 2px;
            width: 10px;
            background-color: $color1;
            transform: rotate(45deg);
        }
        &:after {
            content: "";
            position: absolute;
            top: 3px;
            right: -1px;
            height: 2px;
            width: 10px;
            background-color: $color1;
            transform: rotate(-45deg);
        }
        &--reset {
            transform: rotate(0);
            transition: .6s;
        }
        &--rotate {
            transform: rotate(-180deg);
            transition: .6s;
        }
    }
    &__item {
        display: none;
        margin-top: 15px;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: 0.02em;
        line-height: 2.25;
        color: $color2;
    }
    &__checkbox {
        margin-right: 7px;
    }
    &__scroll {
        position: relative;
        overflow: hidden;
        z-index: 1;
        &__item {
          max-height: 180px;  
          overflow: auto;
        }
    }
    &__pres {
        display: inline-block;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: 0.02em;
        color: $color2;
    }
    &__select {
        border: none;
        font-size: 16px;
        font-weight: 700;
        letter-spacing: 0.02em;
        color: $color2;;
        border-bottom: 2px solid currentColor;
        outline: none;
    }
}
